import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { TabsPage } from './tabs.page';

const routes: Routes = [
  {
    path: 'tabs',
    component: TabsPage,
    children: [
      {
        path: 'home',
        loadChildren: () => import('../Components/Tourist/home/home.module').then( m => m.HomePageModule)
      },
      {
        path: 'city-details/:id',
        loadChildren: () => import('../Components/Tourist/city-details/city-details.module').then( m => m.CityDetailsPageModule)
      },
      {
        path: 'gov-home',
        loadChildren: () => import('../Components/Governrate/gov-home/gov-home.module').then( m => m.GovHomePageModule)
      },
      {
        path: 'learn-about/:id',
        loadChildren: () => import('../Components/Tourist/learn-about/learn-about.module').then( m => m.LearnAboutPageModule)
      },
      {
        path: '',
        redirectTo: '/tabs/home',
        pathMatch: 'full'
      }
    ]
  },
  {
    path: '',
    redirectTo: '/tabs/home',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
})
export class TabsPageRoutingModule {}
